import {
  Directive,
  Input,
  OnChanges,
  ElementRef,
  Renderer,
  NgModule,
  ModuleWithProviders,
  SimpleChanges
} from '@angular/core';


export class MdlBadgeDirective implements OnChanges {

  private el: HTMLElement;

   public mdlBadgeContent: string;

  constructor(private elementRef: ElementRef, private renderer: Renderer) {
    this.el = elementRef.nativeElement;
  }

  public ngOnChanges(changes: SimpleChanges) {
    this.renderer.setElementAttribute(this.el, 'data-badge', this.mdlBadgeContent);
  }

static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: '[mdl-badge]',
  host: {
    '[class.mdl-badge]': 'true'
  }
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, },
{type: Renderer, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'mdlBadgeContent': [{ type: Input, args: ['mdl-badge', ] },],
};
}


